const mathStr = `How to fix this problem? As KL divergence can be written as:

$$
D_{KL}(q \\Vert p) = \\mathbb{E}_{q}[\\log q] - \\mathbb{E}_{q}[\\log p] = -J_{MLE}[q] - H[q]
$$

we reveal that mode collapse is due to the missing entropy term in KL divergence when
using SDS and VSD. Our remedy is to simply add it back:

$$
J_{Ent} = -J_{MLE}[q] - \\lambda H[p]
$$

Surprisingly, the new score distillation optimizing for $J_{Ent}$  can be derived as adopting CFG
trick on VSD:

$$
\\nabla J_{Ent} = \\mathbb{E} \\left[\\nabla \\log p(x | y) - \\lambda \\nabla \\log q(x | \\emptyset) - (1-\\lambda) \\nabla \\log q(x | c) \\right]
$$
`

